import React from 'react';

type ThreeOptionSelectType ={
    items:ItemsType[]
    value:number
}

type ItemsType = {
    id:number
    title:string
}

export const ThreeOptionSelect = (props:ThreeOptionSelectType) => {

    const TitleFind = props.items.map((el)=>el.id===props.value)


    return (
        <div>
            {TitleFind}
        </div>
    );
};

